import React from 'react';
import DefaultLayout from '../layouts/DefaultLayout';
import {
    Container,
    Row
} from 'reactstrap';
import GeoserverLayerExample from '../modules/examples/GeoserverLayerExample';
import GeoserverLayerFilteringExample from '../modules/examples/GeoserverLayerFilteringExample';

export default () => (
    <DefaultLayout>
        <Container>
            <Row>
                <div className="col">
                    <GeoserverLayerExample />
                    <GeoserverLayerFilteringExample />
                </div>
            </Row>
        </Container>
    </DefaultLayout>
);