import React, { Component } from 'react';
import Prism from 'prismjs';

class Script extends Component {
    componentDidMount() {
        Prism.highlightAll();
    }
    render() {
        const { children } = this.props;
        return <pre>
            <code className='language-javascript'>
                {children}
            </code>
        </pre>;
    }
}

export default Script;