import React, { Component } from 'react';
import GeoserverLayer from 'easyolmaps/layer/GeoserverLayer';
import { Map, View } from 'ol';
import TileLayer from 'ol/layer/Tile';
import OSM from 'ol/source/OSM';
import { fromLonLat } from 'ol/proj';
import { GEOSERVER_LAYER_EXAMPLE_CODE } from '../examplesCode';
import MapContainer from '../components/MapContainer';
import Script from '../../../global/components/Script';
import Highlighted from '../../apidoc/components/Highlighted';

class GeoserverLayerExample extends Component {
    constructor(props) {
        super(props);
        this.map = null;
        this.mapElement = null;
    }
    componentDidMount() {
        this.map = new Map({
            target: this.mapElement,
            layers: [
                new TileLayer({ source: new OSM() }),
                new GeoserverLayer('tiger:poly_landmarks', {
                    url: 'http://gisteco.epsgrau.pe:8080/geoserver/wms'
                })
            ],
            view: new View({
                center: fromLonLat([-73.96168, 40.78606]),
                zoom: 11
            })
        })
    }
    render() {
        return <div>
            <h5 className='my-3'>GeoServer Layer</h5>
            <MapContainer ref={mapElement => this.mapElement = mapElement} />
            <p className='mt-3'>This example shows how to use <Highlighted>easyolmaps/layer/GeoserverLayer</Highlighted> to add a Layer published by GeoServer WMS.</p>
            <Script>{GEOSERVER_LAYER_EXAMPLE_CODE}</Script>
        </div>
    }
}

export default GeoserverLayerExample;